////Basestyles//.alert{position:relative;padding:$alert-padding-y$alert-padding-x;margin-bottom:$alert-margin-bottom;border:$alert-border-widthsolidtransparent;@includeborder-radius($alert-border-radius);}//Headingsforlargeralerts.alert-heading{//Specifiedtopreventconflictsofchanging$headings-colorcolor:inherit;}//Provideclassforlinksthatmatchalerts.alert-link{font-weight:$alert-link-font-weight;}//Dismissiblealerts////Expandtherightpaddingandaccountfortheclosebutton'spositioning..alert-dismissible{padding-right:$alert-dismissible-padding-r;//Adjustcloselinkposition.btn-close{position:absolute;top:0;right:0;z-index:$stretched-link-z-index+1;padding:$alert-padding-y*1.25$alert-padding-x;}}//scss-docs-startalert-modifiers//Generatecontextualmodifierclassesforcolorizingthealert.@each$state,$valuein$theme-colors{$alert-background:shift-color($value,$alert-bg-scale);$alert-border:shift-color($value,$alert-border-scale);$alert-color:shift-color($value,$alert-color-scale);@if(contrast-ratio($alert-background,$alert-color)<$min-contrast-ratio){$alert-color:mix($value,color-contrast($alert-background),abs($alert-color-scale));}.alert-#{$state}{@includealert-variant($alert-background,$alert-border,$alert-color);}}//scss-docs-endalert-modifiers