//Gradients//scss-docs-startgradient-bg-mixin@mixingradient-bg($color:null){background-color:$color;@if$enable-gradients{background-image:var(--#{$variable-prefix}gradient);}}//scss-docs-endgradient-bg-mixin//scss-docs-startgradient-mixins//Horizontalgradient,fromlefttoright////Createstwocolorstops,startandend,byspecifyingacolorandpositionforeachcolorstop.@mixingradient-x($start-color:$gray-700,$end-color:$gray-800,$start-percent:0%,$end-percent:100%){background-image:linear-gradient(toright,$start-color$start-percent,$end-color$end-percent);}//Verticalgradient,fromtoptobottom////Createstwocolorstops,startandend,byspecifyingacolorandpositionforeachcolorstop.@mixingradient-y($start-color:$gray-700,$end-color:$gray-800,$start-percent:null,$end-percent:null){background-image:linear-gradient(tobottom,$start-color$start-percent,$end-color$end-percent);}@mixingradient-directional($start-color:$gray-700,$end-color:$gray-800,$deg:45deg){background-image:linear-gradient($deg,$start-color,$end-color);}@mixingradient-x-three-colors($start-color:$blue,$mid-color:$purple,$color-stop:50%,$end-color:$red){background-image:linear-gradient(toright,$start-color,$mid-color$color-stop,$end-color);}@mixingradient-y-three-colors($start-color:$blue,$mid-color:$purple,$color-stop:50%,$end-color:$red){background-image:linear-gradient($start-color,$mid-color$color-stop,$end-color);}@mixingradient-radial($inner-color:$gray-700,$outer-color:$gray-800){background-image:radial-gradient(circle,$inner-color,$outer-color);}@mixingradient-striped($color:rgba($white,.15),$angle:45deg){background-image:linear-gradient($angle,$color25%,transparent25%,transparent50%,$color50%,$color75%,transparent75%,transparent);}//scss-docs-endgradient-mixins